body{
  padding: 0;
  margin: 0;
}
#app {
  font-family: punctuation,"PingFangSC-Regular","Microsoft Yahei","sans-serif";
  -webkit-font-smoothing: subpixel-antialiased;
  text-align: center;
  color: #2c3e50;
}
.page-title{
  color: #00A0D8;
  font-size: 36px;
  font-weight: bold;
  padding: 30px 0;
}
.page-title a{
  color: #00A0D8;
}

/*列表*/
.mod-parallel{
  overflow: hidden;
}
.mod-parallel .img-list{
  overflow: hidden;
  padding-top: 8px;
}
.mod-parallel .img-list .img-box{
  height: auto;
  border: none;
  width: 33.333333%;
  border-right: 12px solid transparent;
  border-left: 12px solid transparent;
  float: left;
  margin: 0 0 8px 0;
  position: relative;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.mod-parallel .img-list .img-item{
  display: block;
  width: 100%;
  margin: 0;
  background-position: 50% 50%;
  background-color: #19C3FF;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100%;/*宽高比*/
  position: relative;
  border-radius: 999px;
}
.mod-parallel .img-list.type-full{
  margin: 0 -1px;
}
.mod-parallel .img-list.type-full .img-box{
  border-right-width: 1px;
  border-left-width: 1px;
  margin: 0 0 2px 0;
}
.mod-parallel .img-list.type-full .img-item{
  border-radius: 0;
}
.mod-parallel a,
.mod-parallel .page-link{
  position: absolute;
  display: block;
  text-align: center;
  height: 100%;
  top: 0;
  color: #fff;
  text-decoration: none;
  line-height: inherit;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-pack: center;
  -webkit-box-align: center;
}


.mod-module.mod-btn .bar-flex[data-v-98089aaa]{
  display: -webkit-flex; /* Safari */
  display: flex;
}
.mod-module.mod-btn .btn-bar[data-v-98089aaa]{
  padding:5px 12px;
  font-size: 0;
  text-align: center;
}
.mod-module.mod-btn .text-r[data-v-98089aaa]{
  text-align: right;
}
/*
 * Basic reset css for mobile
 * author : cheunglong
 */
html, body, div, span, iframe, h1, h2, h3, h4, h5, h6, p, blockquote,a,address,
em, img, ol, ul, li, fieldset, form, label, legend, table, tbody, tfoot, thead, tr, th, td,i,b,s {
    margin:0; padding:0; border:0; font-weight:inherit; font-style:inherit; font-size:100%;font-family:microsoft yahei,Helvetica,Arial,sans-serif;
}


ul,ol{list-style: none}
a img { border:none;vertical-align: top; }
a {text-decoration: none;}

button {overflow:visible;padding:0;margin:0;border:0 none;background-color:transparent;font-family:microsoft yahei,Helvetica,Arial,sans-serif;}
button::-moz-focus-inner { padding:0;}

input[type=password] {-webkit-text-security:disc;}
textarea:focus,input:focus,button:focus{outline:none;}
body {word-wrap:break-word;}
* {-webkit-tap-highlight-color:rgba(0,0,0,0);}/*QUI button*/
.qui-btn{
    position: relative;
    display: inline-block;
    border-radius: 2px;
    border:1px solid #C3C8CC;
    color: #000;
    box-sizing: border-box;
    font-size: 0;
    z-index:1;
}
.qui-btn .ico{
    display: inline-block;
    background-size: 100% 100%;
    position: relative;
    /*top:50%;*/
    /*-webkit-transform: translateY(-50%);*/
    /*transform: translateY(-50%);*/
    margin-right: 3px;
    width: 20px;
    height: 20px;
    vertical-align: -4px;
}
.qui-btn span {
    font-size: 14px;
    display: inline-block;
    height: 100%;
    /*line-height: 2;*/
    box-sizing: border-box;
}
.qui-btn.bor{
    border-color: transparent;
}
.bor:after{
    content: '';
    position: absolute;
    z-index: 1;
    border-radius:4px;
    background-color: transparent;
    border: 1px solid #C3C8CC;
    width: 200%;
    height: 200%;
    -webkit-transform:scale(.5);
    transform:scale(.5);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    /*box-sizing: border-box;*/
    /*left: 0;*/
    /*top: 0;*/
    padding: 1px;
    left: -1px;
    top: -1px;
}
/*小尺寸按钮*/
.qui-btn.small{
    min-width: 60px;
    text-align: center;
    height: 30px;
    line-height: 30px;
    /*margin: 0 5px;*/
}
/*带icon的按钮*/
.qui-btn.icon{
    padding: 0 10px;
    min-width: auto;
    width: auto;
}
/*大尺寸按钮--一行展示一个*/
.qui-btn.large{
    text-align: center;
    height: 40px;
    line-height: 40px;
    /*margin: 0 12px;*/
    display: block;
    width: 100%;
}
.qui-btn.large span {
    font-size: 18px;
}
/*中尺寸按钮--一行多个*/
.qui-btn.middle{
    text-align: center;
    height: 40px;
    line-height: 40px;
    margin: 0 6px;
    flex: 1;
    padding: 0;
}
.qui-btn.middle span {
    font-size: 18px;
}
/*带背景色的按钮*/
.qui-btn.bg:before{
    content: '';
    display: block;
    position: absolute;
    background-color: #eee;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}
.qui-btn.bg.bor:before{
    padding: 1px;
    left: -1px;
    top: -1px;
}











































